.main-header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px;
  background-color: rgba(207, 202, 202, 1.0);
  z-index: 1000;
}

.logo {
  font-size: 20px;
  font-weight: bold;

}

span {
  color: #2E5DA3;
}

.navigation {
  display: flex;
  align-items: center;
}

.navigation a {
  margin-right: 20px;
  /* Adjust the spacing between links */
  text-decoration: none;
  transition: color 0.3s ease;
  color: white;
}

.navigation a:hover {
    color: #666; /* Change color on hover */
}